<template>
  <div class="video-display">
    <span v-if="!token">未获取监控地址，请刷新后尝试！</span>
    <div v-if="token" class="video" :id="'videodisplay-' + id" v-show="playing"></div>
    <div v-if="token" class="video-play" v-show="!playing" @click="videoPlayer"></div>
  </div>
</template>

<script>
import { OpenVidu } from "openvidu-browser";

export default {
  name: 'VideoDisplay',
  props: {
    id: {
      type: String,
      default: "" + Date.now()
    },
    token: {
      type: String,
      default: null
    },
    muted: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      openVidu: null,
      session: null,
      loading: true,
      playing: false
    };
  },
  watch: {
    token(val) {
      val && this.init(val);
    },
    muted(val) {
      const video = document.querySelector("#videodisplay-" + this.id + " video");
      if (this.video) {
        this.video.muted = val;
      }
    }
  },
  mounted() {
    this.init(this.token);
  },
  methods: {
    init(token) {
      if (!token) {
        return;
      }
      const that = this;
      this.openVidu = new OpenVidu();
      this.session = this.openVidu.initSession();
      console.log('videodisplay', token, 'init');
      this.session.on("streamCreated", event => {
        console.log('videodisplay # streamCreated', that.id);
        const subscriber = that.session.subscribe(event.stream, "videodisplay-" + that.id);
        subscriber.on("videoElementCreated", event => {
          that.loading = false;
          const $video = event.element;
          $video.muted = that.muted;
          $video.onplay = function (e) {
            that.playing = true;
          };

          $video.onplaying = function (e) {
            that.playing = true;
          };

          $video.onpause = function (e) {
            that.playing = false;
          };

          $video.onended = function (e) {
            that.playing = false;
          };

          $video.onerror = function (e) {
            that.playing = false;
          };
          // $video.controls = "controls";
        });
      });

      this.session.on("streamDestroyed", event => {
        console.info("# >>>>", event.target.sessionId);
        that.$emit('destroy');
        that.loading = true;
      });

      this.session.connect(token).then(() => {
        console.log("connect success");
      }).catch(err => {
        console.error("connect failed", err.code, err.message);
      });
    },
    reload() {
      this.destory();
      const that = this;
      setTimeout(() => {
        that.init(that.token);
      }, 1000);
    },
    videoPlayer() {
      let video = document.querySelector("#videodisplay-" + this.id + " video");
      video && video.play();
    },
    fullScreen() {
      let $video = document.querySelector("#videodisplay-" + this.id + " video");
      if ($video) {
        if ($video.requestFullscreen) {
          $video.requestFullscreen();
        } else if ($video.mozRequestFullScreen) {
          $video.mozRequestFullScreen();
        } else if ($video.webkitRequestFullScreen) {
          $video.webkitRequestFullScreen();
        }
      }
    },
    exitFullScreen() {
      let video = document.querySelector("#videodisplay-" + this.id + " video");
      if (video) {
        if (video.exitFullscreen) {
          video.exitFullscreen();
        } else if (video.mozCancelFullScreen) {
          video.mozCancelFullScreen();
        } else if (video.webkitCancelFullScreen) {
          video.webkitCancelFullScreen();
        }
      }
    },
    destory() {
      if (this.session) {
        this.session.disconnect();
        this.openVidu = null;
        this.session = null;
        this.$emit('closed');
      }
    }
  },
  destroyed() {
    this.destory();
  }
}
</script>
<style lang="less">
.video video {
  width: 100%;
  height: 100%;
}
</style>

<style lang="less" scoped>
.video-display {
  width: 790px;
  height: 440px;
  .video {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    span {
      padding-top: 1rem;
      color: #fff;
      font-size: 1rem;
    }
  }
  .video-play {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    background: rgba(0, 0, 0, 0.35) url("./../assets/image/poster_play.jpg")
      no-repeat center;
    background-size: cover;
  }
}
</style>