<template>
  <div class="video">
    <video
      :src="url"
      preload="true"
      v-if="url"
      v-show="playing"
      autoplay
      @play="playing = true"
      @playing="playing = true"
      @pause="playing = false"
      @ended="playing = false"
      @error="playing = false"
    >your browser does not support the video tag</video>
    <div class="player" v-if="url" v-show="!playing" @click="videoPlayer"></div>
    <span v-if="!url">未获取监课回放地址，请刷新后尝试</span>
  </div>
</template>
<script>
export default {
  name: "VideoPlayer",
  props: {
    url: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      playing: false,
    }
  },
  methods: {
    videoPlayer() {
      const that = this;
      var video = document.querySelector('video');
      video.onplay = function (e) {
        that.playing = true;
      };

      video.onplaying = function (e) {
        that.playing = true;
      };

      video.onpause = function (e) {
        that.playing = false;
      };

      video.onended = function (e) {
        that.playing = false;
      };

      video.onerror = function (e) {
        that.playing = false;
      };
      
      video && video.play().catch((err) => {
        that.$emit('error');
      });
    }
  }
}
</script>
<style lang="less" scoped>
.video {
  display: flex;
  justify-content: center;
  align-items: center;
  video,
  .player {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  .player {
    background: rgba(0, 0, 0, 0.35) url("./../assets/image/poster_play.jpg")
      no-repeat center;
    background-size: cover;
  }

  button {
    background: #1e68e8;
    padding: 0.35rem 0.65rem;
    min-width: 3rem;
    border-radius: 1rem;
    font-size: 1rem;
    color: #fff;
    font-weight: bold;
  }

  span {
    padding-top: 1rem;
    color: #fff;
    font-size: 1rem;
  }
}
</style>