import BaseApiService from '@/service/BaseApiService'

class Api extends BaseApiService {
    constructor() {
        super();
    }

    /**
     * 检查课程信息
     */
    getClassInfo = (id) => this.request('playback/monitor_exists', { room_id: id });

    /**心跳 */
    testHeart = () => this.request('playback/heart');
    /**
     * 课程结束检查回放视频
     */
    getPlayBackInfo = (id) => this.request('playback/info', { room_id: id });
}

export default {
    install(Vue) {
        Vue.prototype.Api = new Api();
        Vue.prototype.$api = Vue.prototype.Api;
    }
}