<template>
  <div class="wrapper">
    <template v-if="checked">
      <h1 class="title">{{title}}</h1>
      <div class="container" :style="{zoom:page.ratio}">
        <!-- 监课组件 -->
        <video-display
          class="fill"
          v-if="loading && playing"
          :id="classId"
          :token="token"
          :muted="false"
          @closed="videoClosed"
          @destroy="videoDestroy"
        ></video-display>

        <!-- 回放视频 -->
        <video-player class="fill" :url="url" v-if="loading && !playing" @error="errorHandler"></video-player>

        <span v-if="!loading" class="loading">loading...</span>
      </div>

      <div class="logo">
        <img :src="logo">
      </div>

      <BaseWindow
        class="alert"
        label="刷新"
        @change="refreshClick"
        v-if="loading && !playing"
        v-show="!url"
      >暂时还不能观看，请稍后重试～</BaseWindow>

      <BaseWindow
        v-if="playerror"
        class="alert"
        label="刷新"
        @change="refreshClick"
      >当前课程回放播放失败，请反馈到我们，我们来尽快帮您处理，谢谢您的谅解 拨打400-163-6161</BaseWindow>
    </template>

    <template v-if="!checked">
      <span>{{title}}</span>
    </template>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Bowser from "bowser";

import bottom_logo from "./assets/image/logo_bottom.png";
import BaseWindow from "./views/BaseWindow.vue";
import VideoDisplay from "./views/VideoDisplay.vue";
import VideoPlayer from "./views/VideoPlayer.vue";
export default {
  name: "app",
  components: {
    BaseWindow,
    VideoDisplay,
    VideoPlayer
  },
  data() {
    return {
      checked: false,
      loading: false,
      classId: null,
      title: "",
      token: null,
      url: null,
      logo: bottom_logo,
      heartTimeId: 0,
      playerror: false
    };
  },
  computed: {
    ...mapState("page", {
      page: state => state
    })
  },
  methods: {
    ...mapActions("page", ["onResize"]),
    init(id) {
      const that = this;
      that.classId = id;
      this.$api.getClassInfo(id).then(({ isSuccess, result }) => {
        that.loading = true;
        if (isSuccess) {
          that.title = result.title;
          that.playing = result["exists"];
          if (result["exists"]) {
            //有监课
            that.token = result.token;
            that.heartTimeId = setInterval(() => {
              that.$api.testHeart();
            }, 5000);
          } else {
            that.getVideoInfo();
          }
        }
      });
    },
    videoDestroy() {
      this.loading = false;
      this.url = null;
      this.init(this.classId);
    },
    videoClosed() {
      clearInterval(this.heartTimeId);
      this.heartTimeId = 0;
    },
    getVideoInfo() {
      const that = this;
      this.$api.getPlayBackInfo(that.classId).then(({ isSuccess, result }) => {
        if (isSuccess) {
          that.url = result.url;
        } else {
          // that.getVideoInfo();
        }
      });
    },
    refreshClick() {
      window.location.reload();
    },
    errorHandler() {
      this.playerror = true;
    }
  },
  mounted() {
    this.onResize(document.documentElement);
    window.onresize = () => {
      this.onResize(document.documentElement);
    };

    const bowser = Bowser.getParser(navigator && navigator.userAgent);
    const browser = bowser.getBrowser();
    if (browser.name == "Safari" && Number(browser.version.split(".")[0]) < 11) {
      this.checked = false;
      this.title = "发现您手机系统版本过低，不能进行家长监课；请先将您的手机系统升级到最新版本，再来观看吧～";
    } else {
      //查找课程ID
      const reg = new RegExp("\\/playback\\/(\\d+)\\/", "gi");
      const result = reg.exec(window.location.href);
      if (result) {
        this.checked = true;
        this.init(result[1]);
      } else {
        this.title = "无效课程地址";
      }
    }
  }
};
</script>

